.ButtonIcon {
position: absolute;
top: 40px;
right: 40px;
width: 30px;
height: 30px;
display: flex;
align-items: center;
justify-content: center;
border-radius: 50%;
outline: none;
}

.ButtonIcon:hover,
.ButtonIcon:focus {
    border: 1px solid crimson;
    background-color: crimson;
}